import React, { useMemo } from 'react'

import {
  DeleteOutlined,
  EyeOutlined,
  EyeInvisibleOutlined,
} from '@ant-design/icons'

import { TxTracker } from 'redux/midgard/types'

import { useTxManager } from 'hooks/useTxManager'

import { Label } from '../UIElements'
import * as Styled from './TxManager.style'
import { TxMonitor } from './TxMonitor'

export const TxManager = () => {
  const {
    txTrackers,
    txCollapsed,
    setTxCollapsed,
    clearTxTrackers,
  } = useTxManager()

  const toggle = React.useCallback(() => {
    setTxCollapsed(!txCollapsed)
  }, [setTxCollapsed, txCollapsed])

  const hasTxHistory = useMemo(() => txTrackers.length > 0, [txTrackers])

  const renderAllTxTrackers = useMemo(
    () =>
      txTrackers.map((tracker: TxTracker) => (
        <TxMonitor key={tracker.uuid} txTracker={tracker} />
      )),
    [txTrackers],
  )

  const handleClearHistory = React.useCallback(() => {
    clearTxTrackers()
  }, [clearTxTrackers])

  return (
    <Styled.Container collapsed={txCollapsed}>
      <Styled.StyledButton onClick={toggle} round>
        {txCollapsed ? <EyeOutlined /> : <EyeInvisibleOutlined />}
      </Styled.StyledButton>
      {!txCollapsed && (
        <>
          {!hasTxHistory && (
            <Styled.EmptyContent>
              <Label>No transactions.</Label>
            </Styled.EmptyContent>
          )}
          {hasTxHistory && (
            <Styled.Content>
              {renderAllTxTrackers}
              <Styled.ClearRow onClick={handleClearHistory}>
                <Label>Clear History</Label>
                <DeleteOutlined />
              </Styled.ClearRow>
            </Styled.Content>
          )}
        </>
      )}
    </Styled.Container>
  )
}
