import React, { useEffect, useMemo, useState, useCallback } from 'react'

import { useHistory, useParams } from 'react-router'

import { SwapOutlined } from '@ant-design/icons'
import {
  AddressSelectCard,
  AssetInputCard,
  Slider,
  ConfirmModal,
  Information,
  Notification,
  IconButton,
  FancyButton,
  ProtocolSelect,
} from 'components'
import {
  getInputAssets,
  Amount,
  Asset,
  AssetAmount,
  getWalletAddressByChain,
  Swap,
  Percent,
  Price,
  getAssetBalance,
  SupportedChain,
  hasWalletConnected,
  hasConnectedWallet,
} from 'multichain-sdk'
import styled from 'styled-components'
import { palette } from 'styled-theme'

import { useApp } from 'redux/app/hooks'
import { useMidgard } from 'redux/midgard/hooks'
import { TxTrackerStatus, TxTrackerType } from 'redux/midgard/types'

import { useApprove } from 'hooks/useApprove'
import { useBalance } from 'hooks/useBalance'
import { useMimir } from 'hooks/useMimir'
import { useNetworkFee } from 'hooks/useNetworkFee'
import { useTxTracker } from 'hooks/useTxTracker'

import { multichain } from 'services/multichain'

import { translateErrorMsg } from 'helpers/error'
import { truncateAddress } from 'helpers/string'

import { IS_SYNTH_ACTIVE, IS_TESTNET } from 'settings/config'
import { getSwapRoute } from 'settings/constants'
import {
  TX_FEE_TOOLTIP_LABEL,
  SLIP_TOOLTIP_LABEL,
  // ESTIMATED_TIME_LABEL,
  MIN_RECEIVED_LABEL,
} from 'settings/constants/label'

import { PanelView } from './PanelView'
import * as Styled from './Swap.style'
import { Pair } from './types'
import { getSwapPair, getSwapTrackerType } from './utils'

const SwapView = () => {
  const { pair } = useParams<{ pair: string }>()
  const [swapPair, setSwapPair] = useState<Pair>()

  useEffect(() => {
    const getPair = async () => {
      const swapPairData = await getSwapPair(pair)

      if (swapPairData) {
        setSwapPair(swapPairData)
      }
    }

    getPair()
  }, [pair])

  if (swapPair) {
    const { inputAsset, outputAsset } = swapPair
    return <SwapPage inputAsset={inputAsset} outputAsset={outputAsset} />
  }

  return null
}
const FromCard = styled.div`
  background-image: linear-gradient(
    120deg,
    ${palette('background', 6)},
    ${palette('background', 7)}
  );
  border-radius: 4px;
  height: 145px;
`

const ToCard = styled.div`
  background-image: linear-gradient(
    120deg,
    ${palette('background', 6)},
    ${palette('background', 7)}
  );
  border-radius: 4px;
  height: 145px;
`

const StyledSliderWrapper = styled.div`
  width: 90%;
  margin: auto;
`

const SwapPage = ({ inputAsset, outputAsset }: Pair) => {
  const history = useHistory()
  const { wallet, getMaxBalance } = useBalance()
  const { pools: allPools, poolLoading, inboundData } = useMidgard()
  const { slippageTolerance, affiliates } = useApp()
  const { haltedChains } = useMimir()
  const filteredAffiliates = useMemo(
    () =>
      affiliates?.filter((affiliate) =>
        multichain.validateAddress({
          chain: 'THOR',
          address: affiliate,
        }),
      ),
    [affiliates],
  )
  const { submitTransaction, pollTransaction, setTxFailed } = useTxTracker()
  const { isApproved, assetApproveStatus } = useApprove(inputAsset, !!wallet)
  const isTradingHalted: boolean = useMemo(() => {
    const inTradeInboundData = inboundData.find(
      (data) => data.chain === inputAsset.chain,
    )
    const outTradeInboundData = inboundData.find(
      (data) => data.chain === outputAsset.chain,
    )

    const inTradeMimirStatus =
      haltedChains[inputAsset.L1Chain as SupportedChain]
    const outTradeMimirStatus =
      haltedChains[outputAsset.L1Chain as SupportedChain]
    return (
      (inTradeInboundData?.halted ?? false) ||
      (outTradeInboundData?.halted ?? false) ||
      inTradeMimirStatus ||
      outTradeMimirStatus
    )
  }, [inboundData, haltedChains, inputAsset, outputAsset])

  const { inboundFee, outboundFee, totalFee } = useNetworkFee({
    inputAsset,
    outputAsset,
  })

  const walletConnected = useMemo(
    () => hasWalletConnected({ wallet, inputAssets: [inputAsset] }),
    [wallet, inputAsset],
  )

  const pools = useMemo(
    () => allPools.filter((data) => data.detail.status === 'available'),
    [allPools],
  )
  const poolAssets = useMemo(() => {
    const assets = pools.map((pool) => pool.asset)
    assets.push(Asset.RUNE())

    return assets
  }, [pools])

  const synthAssets = useMemo(() => {
    return pools.map((pool) => {
      const { asset } = pool
      const synthAsset = new Asset(asset.chain, asset.symbol, true)
      synthAsset.isSynth = true

      return synthAsset
    })
  }, [pools])

  const outputAssets = useMemo(() => {
    if (IS_SYNTH_ACTIVE) {
      return [...poolAssets, ...synthAssets]
    }
    return poolAssets
  }, [poolAssets, synthAssets])

  const inputAssets = useMemo(
    () =>
      hasConnectedWallet(wallet)
        ? getInputAssets({ wallet, pools })
        : outputAssets,
    [wallet, pools, outputAssets],
  )

  const [inputAmount, setInputAmount] = useState<Amount>(
    Amount.fromAssetAmount(0, 8),
  )
  const [percent, setPercent] = useState(0)
  const [chainRecipient, setChainRecipient] = useState('')
  const [recipient, setRecipient] = useState('')
  const [visibleConfirmModal, setVisibleConfirmModal] = useState(false)
  const [visibleApproveModal, setVisibleApproveModal] = useState(false)
  const affiliateFee = useMemo(() => inputAmount.div(100), [inputAmount])
  const totalFeePlusAffiliate = useMemo(
    () =>
      totalFee
        .add(new AssetAmount(inputAsset, affiliateFee))
        .totalPriceIn(Asset.USD(), pools),
    [affiliateFee, inputAsset, pools, totalFee],
  )
  const isValidAddress = useMemo(
    () =>
      multichain.validateAddress({
        chain: outputAsset.L1Chain,
        address: recipient,
      }),
    [outputAsset, recipient],
  )

  const swap: Swap | null = useMemo(() => {
    if (poolLoading) return null

    try {
      const inputAssetAmount = new AssetAmount(inputAsset, inputAmount)

      const inboundFeeInInputAsset = new AssetAmount(
        inputAsset,
        Amount.fromAssetAmount(
          inboundFee.totalPriceIn(inputAsset, pools).price,
          inputAsset.decimal,
        ),
      )

      const outboundFeeInOutputAsset = outboundFee
        ? new AssetAmount(
            outputAsset,
            Amount.fromAssetAmount(
              outboundFee.totalPriceIn(outputAsset, pools).price,
              outputAsset.decimal,
            ),
          )
        : new AssetAmount(
            outputAsset,
            Amount.fromAssetAmount(0, outputAsset.decimal),
          )

      return new Swap({
        inputAsset,
        outputAsset,
        pools,
        amount: inputAssetAmount,
        slip: slippageTolerance,
        fee: {
          inboundFee: inboundFeeInInputAsset,
          outboundFee: outboundFeeInOutputAsset,
        },
      })
    } catch (error) {
      console.log(error)
      return null
    }
  }, [
    inputAsset,
    outputAsset,
    pools,
    inputAmount,
    slippageTolerance,
    poolLoading,
    inboundFee,
    outboundFee,
  ])

  const outputAmount: Amount = useMemo(() => {
    if (swap) {
      return swap.outputAmountAfterFee.amount
    }

    return Amount.fromAssetAmount(0, 8)
  }, [swap])

  const slipPercent: Percent = useMemo(
    () => (swap ? swap.slip : new Percent(0)),
    [swap],
  )

  const minReceive: Amount = useMemo(
    () => (swap ? swap.minOutputAmount : Amount.fromAssetAmount(0, 8)),
    [swap],
  )

  const inputAssetPriceInUSD = useMemo(
    () =>
      new Price({
        baseAsset: inputAsset,
        pools,
        priceAmount: inputAmount,
      }),
    [inputAsset, inputAmount, pools],
  )

  const outputAssetPriceInUSD = useMemo(
    () =>
      new Price({
        baseAsset: outputAsset,
        pools,
        priceAmount: outputAmount,
      }),
    [outputAsset, outputAmount, pools],
  )

  useEffect(() => {
    if (wallet) {
      const address = getWalletAddressByChain(wallet, outputAsset.L1Chain)
      setRecipient(address || '')
      setChainRecipient(address || '')
    }
  }, [wallet, outputAsset])

  const maxInputBalance: Amount = useMemo(() => getMaxBalance(inputAsset), [
    inputAsset,
    getMaxBalance,
  ])

  const inputAssetBalance: Amount = useMemo(() => {
    if (!wallet) {
      // allow max amount for emulation if wallet is not connected
      return Amount.fromAssetAmount(10 ** 3, 8)
    }

    return getAssetBalance(inputAsset, wallet)
  }, [inputAsset, wallet])

  const handleSelectInputAsset = useCallback(
    (input: Asset) => {
      history.push(getSwapRoute(input, outputAsset))
    },
    [history, outputAsset],
  )

  const handleSelectOutputAsset = useCallback(
    (output: Asset) => {
      history.push(getSwapRoute(inputAsset, output))
    },
    [history, inputAsset],
  )

  const handleSwitchPair = useCallback(() => {
    history.push(getSwapRoute(outputAsset, inputAsset))
  }, [history, inputAsset, outputAsset])

  const handleChangeInputAmount = useCallback(
    (amount: Amount) => {
      if (amount.gt(maxInputBalance)) {
        setInputAmount(maxInputBalance)
        setPercent(100)
      } else {
        setInputAmount(amount)
        setPercent(amount.div(maxInputBalance).mul(100).assetAmount.toNumber())
      }
    },
    [maxInputBalance],
  )

  const handleChangePercent = useCallback(
    (p: number) => {
      setPercent(p)
      const newAmount = maxInputBalance.mul(p).div(100)
      setInputAmount(newAmount)
    },
    [maxInputBalance],
  )

  const handleSelectMax = useCallback(() => {
    handleChangePercent(100)
  }, [handleChangePercent])

  const handleConfirm = useCallback(async () => {
    setVisibleConfirmModal(false)

    if (wallet && swap) {
      const trackerType = getSwapTrackerType(swap)

      // register to tx tracker
      const trackId = submitTransaction({
        type: trackerType,
        submitTx: {
          inAssets: [
            {
              asset: swap.inputAsset.toString(),
              amount: swap.inputAmount.toSignificant(6),
            },
          ],
          outAssets: [
            {
              asset: swap.outputAsset.toString(),
              amount: swap.outputAmountAfterFee.toSignificant(6),
            },
          ],
        },
      })

      try {
        const txHash = await multichain.swap(
          swap,
          recipient,
          filteredAffiliates,
        )

        // start polling
        pollTransaction({
          type: trackerType,
          uuid: trackId,
          submitTx: {
            inAssets: [
              {
                asset: swap.inputAsset.toString(),
                amount: swap.inputAmount.toSignificant(6),
              },
            ],
            outAssets: [
              {
                asset: swap.outputAsset.toString(),
                amount: swap.outputAmountAfterFee.toSignificant(6),
              },
            ],
            txID: txHash,
          },
        })
      } catch (error: any) {
        setTxFailed(trackId)

        // handle better error message
        const description = translateErrorMsg(error?.toString())

        Notification({
          type: 'error',
          message: 'Submit Transaction Failed.',
          description,
          duration: 20,
        })
        console.log(error)
      }
    }
  }, [
    wallet,
    swap,
    recipient,
    filteredAffiliates,
    submitTransaction,
    pollTransaction,
    setTxFailed,
  ])

  const handleCancel = useCallback(() => {
    setVisibleConfirmModal(false)
  }, [])

  const handleConfirmApprove = useCallback(async () => {
    setVisibleApproveModal(false)

    if (wallet) {
      // register to tx tracker
      const trackId = submitTransaction({
        type: TxTrackerType.Approve,
        submitTx: {
          inAssets: [
            {
              asset: inputAsset.toString(),
              amount: '0', // not needed for approve tx
            },
          ],
        },
      })

      try {
        const txHash = await multichain.approveAsset(inputAsset)
        console.log('approve txhash', txHash)
        if (txHash) {
          // start polling
          pollTransaction({
            type: TxTrackerType.Swap,
            uuid: trackId,
            submitTx: {
              inAssets: [
                {
                  asset: inputAsset.toString(),
                  amount: '0', // not needed for approve tx
                },
              ],
              txID: txHash,
            },
          })
        }
      } catch (error) {
        setTxFailed(trackId)
        Notification({
          type: 'open',
          message: 'Approve Failed.',
          duration: 20,
        })
        console.log(error)
      }
    }
  }, [inputAsset, wallet, setTxFailed, submitTransaction, pollTransaction])

  const handleSwap = useCallback(() => {
    if (walletConnected && swap) {
      if (swap.hasInSufficientFee) {
        Notification({
          type: 'info',
          message: 'Swap Insufficient Fee',
          description: 'Input amount is not enough to cover the fee',
        })
        return
      }

      if (!isValidAddress) {
        Notification({
          type: 'error',
          message: 'Invalid Recipient Address',
          description: 'Recipient address should be a valid address.',
        })
        return
      }

      setVisibleConfirmModal(true)
    } else {
      Notification({
        type: 'info',
        message: 'Wallet Not Found',
        description: 'Please connect wallet',
      })
    }
  }, [isValidAddress, walletConnected, swap])

  const handleApprove = useCallback(() => {
    if (walletConnected && swap) {
      setVisibleApproveModal(true)
    } else {
      Notification({
        type: 'info',
        message: 'Wallet Not Found',
        description: 'Please connect wallet',
      })
    }
  }, [walletConnected, swap])

  const isValidSwap = useMemo(() => {
    if (isTradingHalted && !IS_TESTNET) {
      return {
        valid: false,
        msg: 'Swap not available',
      }
    }

    return swap?.isValid() ?? { valid: false }
  }, [swap, isTradingHalted])

  const isValidSlip = useMemo(() => swap?.isSlipValid() ?? true, [swap])

  const btnLabel = useMemo(() => {
    if (isValidSwap.valid || inputAmount.eq(0)) {
      if (inputAsset.isSynth && outputAsset.isSynth) {
        return 'Swap'
      }
      if (inputAsset.isSynth) {
        return 'Redeem'
      }
      if (outputAsset.isSynth) {
        return 'Mint'
      }
      return 'Swap'
    }

    return isValidSwap?.msg ?? 'Swap'
  }, [isValidSwap, inputAmount, inputAsset, outputAsset])

  // const estimatedTime = useMemo(
  //   () =>
  //     getEstimatedTxTime({
  //       chain: inputAsset.L1Chain as SupportedChain,
  //       amount: inputAmount,
  //     }),
  //   [inputAsset, inputAmount],
  // )

  const renderConfirmModalContent = useMemo(() => {
    return (
      <Styled.ConfirmModalContent>
        <Information
          title="Send"
          description={`${inputAmount.toSignificant(
            6,
          )} ${inputAsset.name.toUpperCase()}`}
        />
        <Information
          title="Recipient"
          description={`${outputAmount.toSignificant(
            6,
          )} ${outputAsset.name.toUpperCase()}`}
        />
        <br />
        <Information
          title="Recipient Address"
          description={truncateAddress(recipient)}
          error={!isValidAddress}
        />
        <br />
        <Information
          title="Slip"
          description={slipPercent.toFixed(3)}
          error={!isValidSlip}
          tooltip={SLIP_TOOLTIP_LABEL}
        />
        <Information
          title="Min Received"
          description={`${minReceive.toSignificant(
            6,
          )} ${outputAsset.name.toUpperCase()}`}
          tooltip={MIN_RECEIVED_LABEL}
        />
        <Information
          title="Transaction Fee"
          description={inboundFee.toCurrencyFormat()}
          tooltip={TX_FEE_TOOLTIP_LABEL}
        />
        <Information
          title="Affiliate Fee"
          description={`${
            affiliateFee.toSignificant(4) ?? ''
          } ${inputAsset.name.toUpperCase()}`}
          tooltip="Transaction fee of 1% used to pay affiliates and fund Crosschain.Quest development."
        />
        <Information
          title="Network Fee"
          description={outboundFee?.toCurrencyFormat() ?? ''}
          tooltip="Thorchain network fee used to pay outbound transaction"
        />
        <Information
          title="Total"
          description={totalFeePlusAffiliate.toCurrencyFormat(2) ?? ''}
          tooltip="Sum of both transaction fee and network fee"
        />
        {/* <Information
          title="Total Fee"
          description={totalFeeInUSD.toCurrencyFormat(4) ?? ''}
          tooltip="Sum of both transaction fee and network fee"
        /> */}
        {/* <Information
          title="Estimated Time"
          description={estimatedTime}
          tooltip={ESTIMATED_TIME_LABEL}
        /> */}
      </Styled.ConfirmModalContent>
    )
  }, [
    inputAmount,
    outputAmount,
    inputAsset,
    outputAsset,
    recipient,
    slipPercent,
    isValidSlip,
    minReceive,
    inboundFee,
    outboundFee,
    // totalFeeInUSD,
    // estimatedTime,
    affiliateFee,
    isValidAddress,
    totalFeePlusAffiliate,
  ])

  const renderApproveModal = useMemo(() => {
    return (
      <Styled.ConfirmModalContent>
        <Information
          title={`Approve ${inputAsset.name.toUpperCase()}`}
          description=""
        />
        <Information
          title="Transaction Fee"
          description={inboundFee.toCurrencyFormat()}
          tooltip={TX_FEE_TOOLTIP_LABEL}
        />
      </Styled.ConfirmModalContent>
    )
  }, [inboundFee, inputAsset])

  const title = useMemo(
    () => `Swap ${inputAsset.name} >> ${outputAsset.name}`,
    [inputAsset, outputAsset],
  )
  const poolAsset = useMemo(
    () => (inputAsset.isRUNE() ? outputAsset : inputAsset),
    [inputAsset, outputAsset],
  )

  return (
    <PanelView meta={title} poolAsset={poolAsset} type="swap">
      <FromCard>
        <AssetInputCard
          title="from"
          asset={inputAsset}
          assets={inputAssets}
          amount={inputAmount}
          balance={inputAssetBalance}
          onChange={handleChangeInputAmount}
          onSelect={handleSelectInputAsset}
          onMax={handleSelectMax}
          usdPrice={inputAssetPriceInUSD}
          wallet={wallet || undefined}
        />
        <StyledSliderWrapper>
          <Slider value={percent} onChange={handleChangePercent} withLabel />
        </StyledSliderWrapper>
      </FromCard>
      <Styled.ToolContainer>
        <Styled.SwitchPair>
          <IconButton onClick={handleSwitchPair}>
            <SwapOutlined />
          </IconButton>
        </Styled.SwitchPair>
      </Styled.ToolContainer>
      <ToCard>
        <AssetInputCard
          title="to"
          asset={outputAsset}
          assets={outputAssets}
          amount={outputAmount}
          onSelect={handleSelectOutputAsset}
          inputProps={{ disabled: true }}
          usdPrice={outputAssetPriceInUSD}
          wallet={wallet || undefined}
        />
        <Styled.RecipientAddrWrapper>
          <AddressSelectCard
            title="address"
            address={recipient}
            chain={outputAsset.L1Chain}
            chainAddr={chainRecipient}
            onAddressChange={setRecipient}
          />
        </Styled.RecipientAddrWrapper>
      </ToCard>
      <Styled.SwapInfo>
        {/* <PriceRate
          price={swap?.price}
          inputAsset={swap?.inputAsset}
          outputAsset={swap?.outputAsset}
        />
        <Information
          title="Slip"
          description={slipPercent.toFixed(3)}
          error={!isValidSlip}
          tooltip={SLIP_TOOLTIP_LABEL}
        />
        <Information
          title="Min Received"
          description={`${minReceive.toSignificant(
            6,
          )} ${outputAsset.name.toUpperCase()}`}
          tooltip={MIN_RECEIVED_LABEL}
        />

        <br />
        <Information
          title="Transaction Fee"
          description={inboundFee.toCurrencyFormat()}
          tooltip={TX_FEE_TOOLTIP_LABEL}
        />
        <Information
          title="Network Fee"
          description={outboundFee?.toCurrencyFormat() ?? ''}
          tooltip="Estimated network fee used to pay outbound transaction"
        /> */}
        <Information
          title="Total Fee"
          description={totalFeePlusAffiliate?.toCurrencyFormat(2) ?? ''}
          tooltip="Sum of both transaction fee and network fee"
        />
      </Styled.SwapInfo>
      <ProtocolSelect />
      {isApproved !== null && wallet && (
        <Styled.ConfirmButtonContainer>
          {!isApproved && (
            <Styled.ApproveBtn
              onClick={handleApprove}
              error={!isValidSwap.valid}
              disabled={
                assetApproveStatus === TxTrackerStatus.Pending ||
                assetApproveStatus === TxTrackerStatus.Submitting
              }
              loading={
                assetApproveStatus === TxTrackerStatus.Pending ||
                assetApproveStatus === TxTrackerStatus.Submitting
              }
            >
              Approve
            </Styled.ApproveBtn>
          )}
          <FancyButton
            disabled={!isApproved}
            onClick={handleSwap}
            error={!isValidSwap.valid}
          >
            {btnLabel}
          </FancyButton>
        </Styled.ConfirmButtonContainer>
      )}
      {!wallet && (
        <Styled.ConfirmButtonContainer>
          <FancyButton onClick={handleSwap} error={!isValidSwap.valid}>
            {btnLabel}
          </FancyButton>
        </Styled.ConfirmButtonContainer>
      )}
      <ConfirmModal
        visible={visibleConfirmModal}
        onOk={handleConfirm}
        onCancel={handleCancel}
        inputAssets={[inputAsset]}
      >
        {renderConfirmModalContent}
      </ConfirmModal>
      <ConfirmModal
        visible={visibleApproveModal}
        onOk={handleConfirmApprove}
        onCancel={() => setVisibleApproveModal(false)}
        inputAssets={[inputAsset]}
      >
        {renderApproveModal}
      </ConfirmModal>
    </PanelView>
  )
}

export default SwapView
